import React from 'react';
import { connect } from 'react-redux';
import { changeCouse } from '../store/actions';

import Title from '../components/title'
import AvaList from '../components/avaList'

import courseStyle from './course.module.scss'

@connect(
  state => ({...state.course})
)
class Course extends React.Component{
  constructor (props) {
    super(props);

    const { dispatch, match: { params: { id } } } = props;
    dispatch(changeCouse(id));
  }

  _renderTitleBlock = (background, title, subTitle) => 
    <div className={ courseStyle.titleBlock } style={ { backgroundImage: `url(${ process.env.PUBLIC_URL + background})` } }>
      <div className={ courseStyle.titleWrap }>
        <h1>{ title }</h1>
        <h2>{ subTitle }</h2>
      </div>
    </div>

  _renderTextContent = (contentList) =>
    <div className={ courseStyle.courseTextContent }>
      { contentList.map((item, index) => <p key={index}>{ item }</p>) }
    </div>

  _renderCourseBlock = (title, content) => 
    <div className={ courseStyle.courseBlock }>
      <Title text={ title } />
      { content }
    </div>

  _renderIntrodution = (title, content) => 
    <div>
      { this._renderCourseBlock(title,
          this._renderTextContent(content)
      ) }
    </div>

  _renderSuit = (title, stuList) => 
    <div>
      {
        this._renderCourseBlock(title,
          <AvaList data={ stuList } grid={ { gutter: 16, column: 4 } }/>
        )
      }
    </div>

  render () {
    const course = {
      background: '/course/course-title.jpg',
      title: '算法基础快速入门(Java/Cpp)',
      subTitle: 'CS301A',
      introduction: {
        title: '课程介绍',
        content: [
          '如果你是毫无写码经验的小白，或者刚刚决定转码，但对接下来要走的路还是一片迷茫，那么算法基础班是你最好的选择。也许很多人会选择学校的算法入门课作为自己转码的第一步，但这并不意味着你真正离offer又近了一步。我们尊重科班的严谨、全面，但是在有限的时间面前，我们更要功利一些。对于学校的算法课程，以学期为单位的进度会让你错过秋招/春招的黄金时期，对于我们的算法基础班，只要集中精力，你可以在不到一个月的时间内完成它。',
          '我们不过分关注造轮子、推公式的过程，不过分关注冗杂的理论分析和数学推导，我们只关注“面试考什么”和“面试官想看到什么”。在一段相当长的时间内，我们非常建议走科班的教学模式，从一砖一瓦开始补起，但如果摆在你面前是迫在眉睫的秋招/春招，那么这个算法基础班或许是你最好的选择。',
          '本课程中除了带领大家走入算法外，还将为大家带来针对该算法所对应的经典面试题，当你掌握了这些经典题目时，你会发现你已经真正的走出了从0到1的一大步。'
        ]
      },
      suit: {
        title: '适合人群',
        studentList: [{
          img: './course/student-icon-1.svg',
          title: '修过CS301A的同学'
        }, {
          img: './course/student-icon-2.svg',
          title: '刷题遇到瓶颈的同学'
        }, {
          img: './course/student-icon-3.svg',
          title: '刷了一遍，但是重做还是忘的同学'
        }, {
          img: './course/student-icon-4.svg',
          title: '面试的时候明明把题目解出来了，但是却屡屡挂掉面试的同学'
        }, {
          img: './course/student-icon-5.svg',
          title: '面试屡屡挂在follow up的同学'
        }, {
          img: './course/student-icon-6.svg',
          title: '醉心钻研难题、偏题，却忽视了基本功的同学'
        }, {
          img: './course/student-icon-7.svg',
          title: '希望系统复习并攻克算法重难点的同学'
        }]
      }
    }

    const intro = course.introduction
    const suit = course.suit

    return (
      <div className={ courseStyle.course }>
        { this._renderTitleBlock(course.background, course.title, course.subTitle) }
        { this._renderIntrodution(intro.title, intro.content) }
        { this._renderSuit(suit.title, suit.studentList) }
      </div>
    )
  }
}

export default Course;
