import React from 'react'
import { Link } from 'react-router-dom'

import headerStyle from './header.module.sass'

import { Menu, Icon } from 'antd';

const { SubMenu, Item } = Menu;

class Header extends React.Component{
  _menuList = [{
    title: '首页',
    href: '/',
    subList: []
  }, {
    title: '核心课程',
    href: '',
    subList: [
      {
        title: 'CS301A 算法基础快速入门(Java/Cpp)',
        href: '/course/CS301A',
        subList: []
      }, {
        title: 'CS301B 题型技巧与算法讲解(Java/Cpp)',
        href: '/course/CS301B',
        subList: []
      }, {
        title: 'CS403A LeetCode1-300高频题详解(Java/Cpp/Python)',
        href: '/course/CS403A',
        subList: []
      }, {
        title: 'CS403B LeetCode301-600高频题详解(Java/Cpp)',
        href: '/course/CS403B',
        subList: []
      }
    ]
  }, {
    title: '专题课程',
    href: '',
    subList: [
      {
        title: 'CS202 二分搜索/字符串/链表(Java/Cpp)',
        href: '/course/CS202',
        subList: []
      }, {
        title: 'CS203 队列/优先队列/栈/树(Java/Cpp)',
        href: '/course/CS203',
        subList: []
      }, {
        title: 'CS204 BFS/DFS/HashTable(Java/Cpp)',
        href: '/course/CS204',
        subList: []
      }, {
        title: 'CS205 动态规划(Java)',
        href: '/course/CS205',
        subList: []
      }
    ]
  }, {
    title: '简历修改',
    href: '',
    subList: [
      {
        title: 'CS105 简历修改与指导',
        href: '/course/CS105',
        subList: []
      }
    ]
  }, {
    title: '直播课',
    href: '',
    subList: [
      {
        title: 'CS501 刷题&算法直播课',
        href: '/course/CS501',
        subList: []
      }
    ]
  }]

  _renderMenu = arrList => 
    arrList.map((item, index) => 
      <Item key={index}>
        <Link to={item.href}>{item.title}</Link>
      </Item>)

  _renderNav = () => (
    this._menuList.map((item, index) => {
      if (item.subList.length === 0) {
        return (
          <Item key={index}>
            <Link to={item.href}>{ item.title }</Link>
          </Item>
        )
      } else {
        return (
          <SubMenu
            key={index}
            title={
              <span slot="title" className="submenu-title-wrapper">
                { item.title }
                <Icon type="down" />
              </span>
            }
          >
            { this._renderMenu(item.subList) }
          </SubMenu>
        )
      }
    })
  )

  render () {
    return (
      <div className={headerStyle.header}>
        <Link to="/">
          <img
            className={headerStyle.logo}
            src="./logo.png"
            alt="logo"
          />
        </Link>
        <Menu mode="horizontal" className={headerStyle.nav} selectable={false} subMenuCloseDelay={0.05}>
          { this._renderNav() }
        </Menu>
      </div>
    )
  }
}

export default Header;
